/* App.css */

/* General Styles */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
  color: #333;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  margin-top: 40px;
}

/* Heading */
h1 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 28px;
}

/* Input and Select Styles */
input[type="text"],
textarea,
select {
  width: 90%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  background-color: #f9f9f9;
}

/* Button Styles */
button {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  width: 100%;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

button.secondary {
  background-color: #007bff;
  margin-top: 10px;
}

button.secondary:hover {
  background-color: #0069d9;
}

/* Question and Answer Layout */
.question-box {
  padding: 15px;
  background-color: #f7f7f7;
  margin-bottom: 15px;
  border-radius: 5px;
}

.question-box p {
  font-size: 18px;
  font-weight: bold;
  color: #555;
}

textarea {
  height: 100px;
}

label {
  display: block;
  margin: 10px 0;
  font-size: 16px;
}

input[type="radio"] {
  margin-right: 10px;
}

/* PDF Download Button */
button.pdf-download {
  background-color: #ff5722;
  margin-top: 20px;
  width: auto;
}

button.pdf-download:hover {
  background-color: #e64a19;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  .container {
    padding: 15px;
    margin-top: 20px;
  }

  h1 {
    font-size: 24px;
  }

  input[type="text"],
  textarea,
  select {
    font-size: 14px;
    padding: 8px;
  }

  button {
    font-size: 14px;
    padding: 10px 16px;
  }
}
