/* Question Box */

.question-box-ateentd {
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Question Text */

.question-box-ateentd p {
  font-size: 18px;
  margin-bottom: 10px;
}

/* MCQ Choices */

.question-box-ateentd label {
  display: block;
  margin-bottom: 10px;
}

.question-box-ateentd input[type="radio"] {
  margin-right: 10px;
}

/* Textarea */

.question-box-ateentd textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 10px;
}

/* Focus States */

.question-box-ateentd input[type="radio"]:focus {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.question-box-ateentd textarea:focus {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  border-color: #aaa;
}
button.new-paper {
  background-color: #28a745;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 15px;
}

button.new-paper:hover {
  background-color: #218838;
}
/* Sticky Navigation Bar with Heading */
.navBar {
  position: sticky;
  top: 0;
  width: auto;
  background-color: #007bff;  /* Blue background */
  display: flex;
  justify-content: center;  /* Center the heading */
  align-items: center;
  padding: 10px 20px;
  z-index: 1000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: white;
  justify-content: space-between;

}

.nab-h1 {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  color: white;  /* White text */
}

/* Optional: Adjust font size for smaller screens */
@media (max-width: 768px) {
  .nab-h1 {
    font-size: 20px;
  }
}
